a {
  height: 100%;
  width: 100%;
  & .project-card {
    border-radius: 2vw;
    width: 100%;
    height: 100%;
    perspective: 1000px;
    &:hover .project-card-inner {
      transform: rotateY(180deg);
    }
    & img {
      width: auto;
      height: 80%;
    }
    & h2 {
      width: 100%;
      height: 30%;
      font-size: 0.8rem;
      @include text-main;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    & .project-card-inner {
      height: 100%;
      width: 100%;
      position: relative;
      transition: transform 0.8s;
      transform-style: preserve-3d;

      & .project-card-front,
      .project-card-back {
        box-shadow: 0 0 10px black;
        border-radius: 2vw;
        position: absolute;
        width: 100%;
        height: 100%;
        backface-visibility: hidden;
        background: linear-gradient(180deg, #fff, #000);
        background-size: cover;
      }
      & .project-card-front {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        padding: 2% 2% 0;
      }
      & .project-card-back {
        transform: rotateY(180deg);
        padding: 3%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        & h2 {
          @include text-main;
          font-size: 250%;
          height: 20%;
        }
        & .stats-tech {
          min-height: max-content;
          max-height: 40%;
          width: 80%;
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          align-items: center;
          & img {
            max-height: 7vmin;
            height: auto;
            padding: 5px;
          }
          & img:nth-child(1) {
            animation: iconMoving 0.3s 1s cubic-bezier(0.39, 0.575, 0.565, 1)
              infinite alternate;
          }
          & img:nth-child(2) {
            animation: iconMoving 0.3s 0.5s cubic-bezier(0.39, 0.575, 0.565, 1)
              infinite alternate;
          }
          & img:nth-child(3) {
            animation: iconMoving 0.3s 0s cubic-bezier(0.39, 0.575, 0.565, 1)
              infinite alternate;
          }
          & img:nth-child(4) {
            animation: iconMoving 0.3s 1s cubic-bezier(0.39, 0.575, 0.565, 1)
              infinite alternate;
          }
          & img:nth-child(5) {
            animation: iconMoving 0.3s 0.5s cubic-bezier(0.39, 0.575, 0.565, 1)
              infinite alternate;
          }
          & img:nth-child(6) {
            animation: iconMoving 0.3s 0s cubic-bezier(0.39, 0.575, 0.565, 1)
              infinite alternate;
          }
          & img:nth-child(7) {
            animation: iconMoving 0.3s 1s cubic-bezier(0.39, 0.575, 0.565, 1)
              infinite alternate;
          }
          & img:nth-child(8) {
            animation: iconMoving 0.3s 0.5s cubic-bezier(0.39, 0.575, 0.565, 1)
              infinite alternate;
          }
          & img:nth-child(9) {
            animation: iconMoving 0.3s 0s cubic-bezier(0.39, 0.575, 0.565, 1)
              infinite alternate;
          }
          & img:nth-child(10) {
            animation: iconMoving 0.3s 1s cubic-bezier(0.39, 0.575, 0.565, 1)
              infinite alternate;
          }
        }
        & .card-back-stats {
          width: 100%;
          height: 30%;
          display: flex;
          justify-content: space-evenly;
          align-items: center;
          & div {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            background-color: rgba(255, 255, 255, 0.7);
            box-shadow: 1px 1px 3px black;
            border-radius: 10px;
            margin: 0 10px;
            width: 30%;
            height: 80%;
            max-width: 100px;
            max-height: 100px;
            & img {
              width: 50px;
              height: 50px;
              padding-bottom: 10px;
            }
            & span {
              color: black;
              font-size: 1.1em;
            }
          }
        }
        & p {
          color: white;
          text-shadow: 1px 1px 1px black;
          font-size: 1.2em;
          margin: 0 5%;
        }
        & .project-button {
          display: none;
        }
      }
    }
  }
}

@keyframes iconMoving {
  100% {
    transform: translateY(-7px);
  }
}

/*/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

                                                    MEDIA QUERIES

/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/

@media screen and (max-width: 1320px) and (orientation: portrait) {
  .project-card {
    .project-card-back {
      h2 {
        font-size: 150% !important;
        height: 20%;
      }
      & .stats-tech {
        max-height: none !important;
        height: fit-content;
        padding: 2vmin 0 !important;
        & img {
          max-height: 11vmin !important;
        }
      }
      .card-back-stats {
        width: 100%;
        height: 20% !important;
        margin: 6% 2% !important;
        div {
          margin: 0 3% !important;
          padding: 1%;
          width: 50% !important;
          height: 80% !important;
          min-height: 70px;
          img {
            width: 40% !important;
            max-width: 45px !important;
            height: 50% !important;
            padding-bottom: 3% !important;
          }
          span {
            font-size: 80% !important;
          }
        }
      }
      p {
        font-size: 90% !important;
      }
      .project-button {
        display: inline-block !important;
        height: min-content;
        width: auto;
        padding: 2% 8%;
        margin-top: 7%;
        margin-bottom: 4%;
        color: #000;
        font-size: 70%;
        box-shadow: 1px 1px 1px #000;
        border-radius: 15px;
        border: #fff solid 1px;
        text-decoration: none;
        background: linear-gradient(
          135deg,
          rgb(255, 255, 255),
          rgb(230, 230, 230)
        );
      }
    }
  }
}

@media screen and (max-width: 1320px) and (orientation: landscape) {
  .project-card {
    h2 {
      font-size: 100% !important;
      height: 20%;
    }
    .project-card-back {
      .stats-tech {
        min-height: 20% !important;
        margin-bottom: -5% !important;
        img {
          max-height: 10vmin !important;
        }
      }
      .card-back-stats {
        height: 20%;
        div {
          margin: 0 5% !important;
          padding: 0 3%;
          width: 30% !important;
          height: 70% !important;
          img {
            width: 40% !important;
            height: 40% !important;
            padding-bottom: 10% !important;
          }
          span {
            font-size: 60%;
          }
        }
      }
      p {
        font-size: 90% !important;
      }
      .project-button {
        display: inline-block !important;
        height: min-content;
        width: auto;
        padding: 2% 8%;
        margin-top: 3%;
        margin-bottom: 2%;
        color: #000;
        font-size: 50%;
        box-shadow: 1px 1px 1px #000;
        border-radius: 15px;
        border: #fff solid 1px;
        text-decoration: none;
        background: linear-gradient(
          135deg,
          rgb(255, 255, 255),
          rgb(230, 230, 230)
        );
      }
    }
  }
}
