@import url('https://fonts.googleapis.com/css2?family=Gochi+Hand&display=swap');

.about-me {
  @include text-main;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: content-box;
  background: fixed transparent;
}

.about-me-title {
  height: 25%;
  display: flex;
  align-items: center;
  padding: 5% 0 2%;
  h1 {
    font-size: 200%;
    font-weight: bold;
    color: white;
    text-shadow: 0.5vw 0.5vw 1vw black;
  }
}

.sections {
  width: 100%;
  height: 65%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Portrait */
@media screen and (max-width: 1370px) and (orientation: portrait) {
  .about-me {
    height: fit-content;
    min-height: 100%;
    padding-bottom: 10vh;
  }
  .about-me-title {
    h1 {
      font-size: 150% !important;
    }
  }
  .sections {
    width: 90%;
    min-height: 60%;
    max-height: 100%;
    transform: translateY(5vmin);
    flex-direction: column;
    justify-content: space-evenly !important;
  }
}

/* Landscape */
@media screen and (orientation: landscape) {
  /* Landscape styles here */
}
