.header {
  height: 5vw;
  width: 100%;
  background: transparent;
  position: absolute;
  top: 0;
  z-index: 2;
  ul {
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 2vw;
    margin-top: 2vh;
    li {
      height: 100%;
      display: flex;
      align-items: center;
      font-size: 1.2vw;
      text-transform: uppercase;
      .nav-link {
        @include link;
      }
      .activeLink {
        @include link(
          $color: rgb(77, 60, 81),
          $textShadow: 1px 1px 1px rgb(255, 255, 255),
          $scale: 1,
          $cursor: default,
          $anim: bgAppear 1s forwards
        );
        &:hover::before {
          width: 0;
          height: 0;
        }
      }
      & .flags-container {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        &:hover {
          transform: scale(1);
        }
        & div {
          width: 80%;
          height: 80%;
          margin: 0 0.1vw;
          transition: transform 0.3s;
          &:hover {
            transform: scale(1.1);
            cursor: pointer;
          }
          & img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}

